<template>
  <div>
    <p v-hekai="color">自定义v-hekai指令改变颜色</p>
    <br />
    点击下列单选按钮课更改颜色
    <p>
      <input type="radio" id="blue" v-model="color" value="blue">
      <label for="blue">蓝色</label>
      <input type="radio" id="orange" v-model="color" value="orange">
      <label for="orange">橘色</label>
      <input type="radio" id="black" v-model="color" value="black">
      <label for="black">黑色</label>
      <input type="radio" id="purple" v-model="color" value="purple">
      <label for="purple">紫色</label>
      <input type="radio" id="gold" v-model="color" value="gold">
      <label for="gold">金色</label>
      <input type="radio" id="red" v-model="color" value="red">
      <label for="red">红色</label>
    </p>
  </div>
</template>

<script>
  export default {
    directives:{
      hekai:{
        inserted:function(el,binding){
          el.style="color:"+binding.value
        },
        update:function (el,binding) {
          el.style="color:"+binding.value
        }
      }
    },
    data(){
      return {
        color:"red"
      }
    }
  }
</script>



