<template>
  <div>
    <p>原始字符串为：{{ msg }}</p>
    <p>使用computed进行倒序处理后显示的为{{ newMsg }}</p>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        //msg:[1,2,3,4,5,6,7,8,9],
        msg:'hello'
      }
    },
    computed:{
      newMsg:function () {
        //return this.msg.reverse()
        return this.msg.split('').reverse().join('')
      }
    }
  }
</script>



