<template>
  <div>
    <p>请添加要展示的条目<button @click="add">加条目</button><button @click="subtract">减条目</button></p>
    <child>
      <p v-for="item in items">已有条目{{ item }}</p>
    </child>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        msg:0,
        items:[]
      }
    },
    components:{
      'child':{template:`<div>我是要展示的内容:<br>
                    <slot>当没有内容是显示我(slot标签里面的内容)</slot>
                </div>`}
    },
    methods:{
      add:function () {
        this.msg++
        this.items.push(this.msg)
      },
      subtract:function () {
        if(this.msg>0){
          this.msg--
          this.items.pop(this.msg)
        }

      }
    }
  }
</script>



