import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import forIfShow from '@/components/v-for & v-if & & v-show'
import textHtmlModel from '@/components/v-text & v-html & v-model'
import myOn from '@/components/v-on'
import myBind from '@/components/myBind'
import vHekai from '@/components/v-hekai'
import componentHekai from '@/components/componentsHekai'
import myProps from '@/components/myProps'
import myIs from '@/components/myIs'
import vueLife from '@/components/vueLife'
import myComputed from '@/components/myComputed'
import myWatch from '@/components/myWatch'
import mySlot from '@/components/mySlot'
import myVueRouterChild from '@/components/myVueRouter/myVueRouterChild'
import child1 from '@/components/myVueRouter/child1'
import child2 from '@/components/myVueRouter/child2'
import child3 from '@/components/myVueRouter/child3'
import child4 from '@/components/myVueRouter/child4'
//import myVuex from '@/components/myVuex/myVuex'


Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'helloWorld',
      alias:'/HelloWorld',
      routes:'/HelloWorld',
      component: HelloWorld,
    },
    {
      path:'/forIfShow',
      name:'forIfShow',
      component:forIfShow
    },
    {
      path:'/textHtmlModel',
      name:'textHtmlModel',
      component:textHtmlModel
    },{
      path:'/myOn',
      name:'myOn',
      component:myOn
    },{
      path:'/myBind',
      name:'myBind',
      component:myBind
    },{
      path:'/vHekai',
      name:'vHekai',
      component:vHekai
    },{
      path:'/componentHekai',
      name:'componentHekai',
      component:componentHekai
    },{
      path:'/myProps',
      name:'myProps',
      component:myProps
    },{
      path:'/myIs',
      name:'myIs',
      component:myIs
    },{
      path:'/vueLife',
      name:'vueLife',
      component:vueLife
    },{
      path:'/myComputed',
      name:'myComputed',
      component:myComputed
    },{
      path:'/myWatch',
      name:'myWatch',
      component:myWatch
    },{
      path:'/mySlot',
      name:'mySlot',
      component:mySlot
    },{
      path:'/myVueRouterChild',
      name:'myVueRouterChild',
      component:myVueRouterChild,
      children:[
        {
          path:'child1',
          component:child1
        },
        {
          path:'child2',
          component:child2,
          alias:'aliasRename'
        },{
          path:'child3',
          name:'child3',
          component:child3
        },{
          path:'child4/:one/:two',
          name:'child4',
          component:child4
        },{
          path:'child5',
          redirect:'/myVueRouterChild/child2'
        },{
          path:'*',
          component:{
            template:`<div>我是404错误页面，请重新选择</div>`
          }
        }
      ]
    },
    // {
    //   path:'/myVuex',
    //   name:'myVuex',
    //   component:myVuex
    // }
  ]
})



