<template>
  <father></father>
</template>

<script>
  let son={
    template:`<p>我是子元素中显示输入框值的插值:{{ sonMsg }}</p>`,
    props:['sonMsg']
  }
  export default{
    components:{
      'father':{
        template:`<p>我是父元素中的输入框:<input type="text" v-model="msg">
                    <son v-if="true" :sonMsg="msg"></son></p>`,
        data(){
          return {
            msg:''
          }
        },
        components:{
          'son':son
        }
     }
    }
  }
</script>



