<template>
  <div>
    <p>温度：{{ msg }}°</p>
    <p>穿衣提示:{{ hint }}</p>
    <button @click="add">加</button>
    <button @click="subtract">减</button>
  </div>

</template>

<script>
  export default{
    data(){
      return {
        msg:12,
        hint:'长衣长裤'
      }
    },
    methods:{
      add:function () {
        this.msg++
      },
      subtract:function () {
        this.msg--
      }
    },
    watch:{
      msg:function (val,oldval) {
        //console.log('新数据'+val+'  老数据'+oldval);
        if(val>=20){
          this.hint='短衣短裤'
        }else if(val>=0){
          this.hint='长衣长裤'
        }else{
          this.hint='棉衣棉裤'
        }
      }
    }
  }
</script>



