<template>
  <div>
    <div v-bind:style="bgColor"></div>
    <p>
      选择下面的按钮改变方块的颜色:
    </p>
    <button @click="blue">蓝色</button>
    <button @click="green">绿色</button>
    <button @click="red">红色</button>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        bgColor:{
          width:'100px',
          height:'100px',
          background:'red'
        }
      }
    },
    methods:{
      blue(){
        this.bgColor.background="blue"
      },
      green(){
        this.bgColor.background="green"
      },
      red(){
        this.bgColor.background="red"
      }
    }
  }
</script>



