<template>
  <div>
    <p>{{ msg }}</p>
    <p>请输入向第三个页面传递的值<input type="text" v-model="newMsg"></p>
    <router-link :to="{name:'child3',params:{myValue:newMsg}}">确认传递</router-link>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        msg:'我是子页面二',
        newMsg:''
      }
    }
  }
</script>



