<template>
  <div>
    <ul>
      <li><router-link to="/myVueRouterChild/child1" class="routerLink">子页面一</router-link></li>
      <li><router-link to="/myVueRouterChild/child2" class="routerLink">子页面二</router-link></li>
      <li><router-link :to="{name:'child3',params:{myValue:'我是直接点第三个导航过来的'}}" class="routerLink">params子页面传参</router-link></li>
      <li><router-link to="/myVueRouterChild/child4/我是直接点第四个导航过来的/我是直接点第四个导航过来的" class="routerLink">子页面URL传参</router-link></li>
      <li><router-link to="/myVueRouterChild/child5" class="routerLink">redirect重定向到子页面二</router-link></li>
      <li><router-link to="/myVueRouterChild/aliasRename" class="routerLink">alias重命名到子页面二</router-link></li>
      <li><router-link to="/myVueRouterChild/fsdfsdf" class="routerLink">我是瞎写的错误路由</router-link></li>
      <li><button @click="routeAdvance" class="routerLink">前进</button></li>
      <li><button @click="retreat" class="routerLink">后退</button></li>
    </ul>
    <router-view class="item"></router-view>
  </div>
</template>

<script>
  export default{
    methods:{
      routeAdvance:function () {
        this.$router.go(+1)
      },
      retreat:function () {
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped>
  div{
    margin-top: 240px;
  }
  ul{
    width: 250px;
    overflow: hidden;
    float: left;
  }
  li{
    width: 100%;
    font: 200 15px/1.5 Helvetica, Verdana, sans-serif;
    border-bottom: 1px solid #ccc;
    text-align: center;
    line-height: 25px;
  }

  li a:hover {
    font-size: 20px;
    background: #f6f6f6;
    color: #f54d27;
  }
  .routerLink{
    text-decoration: none;
    color: #000;
    display: block;
    width: 250px;

    -webkit-transition: font-size 0.3s ease, background-color 0.3s ease;
    -moz-transition: font-size 0.3s ease, background-color 0.3s ease;
    -o-transition: font-size 0.3s ease, background-color 0.3s ease;
    -ms-transition: font-size 0.3s ease, background-color 0.3s ease;
    transition: font-size 0.3s ease, background-color 0.3s ease;
  }
  .router-link-active{
    font-size: 20px;
    background: #f6f6f6;
    color: #f54d27;
  }

  .item{
    margin-left: 290px;
    text-align: left;
  }

</style>



