<template>
  <div>
    <hekai></hekai>
    <hekai1></hekai1>
  </div>
</template>

<script>
  import Vue from 'vue'
  let hekaiExtend=Vue.extend({
    template:`<p>我是extend扩展生成</p>`
  })
  Vue.component('hekai1',{
    template:`<p>这是全局的hekai1标签</p>`
  })
  export default{
    components:{
      hekai:{
        template:`<div>我是局部的hekai标签组件<hekaiExtend></hekaiExtend></div>`,
        components:{
          'hekaiExtend':hekaiExtend
        }
      }
    }
  }

</script>



