<template>
  <div id="app">
    <leftNavigation/>
    <div class='right'>
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
  import leftNavigation from '@/components/communal/leftNavigation'
export default {
  name: 'app',
  components:{
    'leftNavigation': leftNavigation
  },

}
</script>

<style>
  *{
    margin:0;
    padding:0;
    list-style: none;
  }
  hr{
    margin: 20px 0;
  }
#app {
  font-family: 'Microsoft YaHei UI', 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 22px;
  letter-spacing:1px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  background-color: white;
  overflow: hidden;
  height: 100%;
}
.right{
  margin-left: 320px;
  padding: 30px 0 0 0;
}
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
    opacity: 0
  }

</style>



