<template>
  <div>
    <h2>v-on做的计数器</h2>
    <br />
    <h3>{{ msg }}</h3>
    <button v-on:click="add">加分</button>
    <button @click="subtract">减分</button>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        msg:0
      }
    },
    methods:{
      add:function(){
        this.msg++;
      },
      subtract(){
        this.msg--;
      }
    }
  }
</script>





