<template>
  <div>
    <ul style="overflow: hidden">
      <li v-for="(item,index) in items" @click="isWho(index)" :class="{'activeLi':isActive==index}" class="noActiveLi">
        {{ item }}
      </li>
    </ul>
    <div class="item">
      <item :is="who"></item>
    </div>
  </div>
</template>

<script>
  let itemOne={
    template:`<p>这是选项一的自定义组件</p>`
  }
  let itemTwo={
    template:`<p>这是选项二的自定义组件</p>`
  }
  let itemThree={
    template:`<p>这是选项三的自定义组件</p>`
  }
  let itemFour={
    template:`<p>这是选项四的自定义组件</p>`
  }
  export default{
    data(){
      return {
        items:['选项一','选项二','选项三','选项四'],
        who:'itemOne',
        isActive:0
      }
    },
    components:{
      'itemOne':itemOne,
      'itemTwo':itemTwo,
      'itemThree':itemThree,
      'itemFour':itemFour
    },
    methods:{
      isWho:function(index){
        switch (index){
          case 0:
            this.isActive=index;
            this.who='itemOne';
            break;
          case 1:
            this.isActive=index;
            this.who='itemTwo';
            break;
          case 2:
            this.isActive=index;
            this.who='itemThree';
            break;
          case 3:
            this.isActive=index;
            this.who='itemFour';
            break;
        }
      },
    }
  }
</script>

<style scoped>
  .noActiveLi{
    border-bottom: 1px black solid;
    width: 100px;
    float: left;
    display: block;
    text-decoration:none;
    text-align: center;
    cursor:pointer;
  }
  .activeLi{
    border: 1px black solid;
    border-bottom:none;
    border-top-left-radius:4px;
    border-top-right-radius:4px;
    width: 100px;
    float: left;
    display: block;
    text-decoration:none;
    text-align: center;
    cursor:pointer;
    color: #f54d27;
  }
  li:hover{
    color: #f54d27;
  }
  .item{
    text-align: center;
    border:1px #000000 solid;
    margin-top: -1px;
    width: 400px;
    border-top:none;
    height:100px;
  }

</style>



