<template>
  <div class="leftNavigation">
    <ul>
      <h2>vue.js学习</h2>
      <li v-for="nav in vueNavigation"><router-link :to="{ path:nav.router }" class="routerLink">{{ nav.title }}</router-link></li>
      <h2>vue-router学习</h2>
      <li v-for="nav in vueRouterNavigation"><router-link :to="{ path:nav.router }" class="routerLink">{{ nav.title }}</router-link></li>
      <!--<h2>vuex学习</h2>-->
      <!--<li v-for="nav in vuexNavigation"><router-link :to="{ path:nav.router }" class="routerLink">{{ nav.title }}</router-link></li>-->
    </ul>
  </div>

</template>

<script>
  export default {
    name:'leftNavigation',
    data(){
      return {
        vueNavigation:[{
          title:'hello',
          router:'/HelloWorld'
        },{
          title:'v-if & v-for & v-show的用法',
          router:'/forIfShow'
        },{
          title:'v-text & v-html & v-model的用法',
          router:'/textHtmlModel'
        },{
          title:'v-on的用法',
          router:'/myOn'
        },{
          title:'v-bind的用法',
          router:'/myBind'
        },{
          title:'自定义指令v-hekai(directive的用法)',
          router:'/vHekai'
        },{
          title:'自定义hekai组件(component和extend的用法)',
          router:'/componentHekai'
        },{
          title:'Props选项（父子组件传递数据）',
          router:'/myProps'
        },{
          title:'is选项做TAB选项卡',
          router:'/myIs'
        },{
          title:'vue的生命周期',
          router:'/vueLife'
        },{
          title:'computed选项做数组倒序',
          router:'/myComputed'
        },{
          title:'watch选项实现数据监控',
          router:'/myWatch'
        },{
          title:'slot标签的用法',
          router:'/mySlot'
        }],
        vueRouterNavigation:[{
          title:'vue-router配置路由全学习(里面有小选项)',
          router:'/myVueRouterChild'
        }],
//        vuexNavigation:[{
//          title:'vuex学习',
//          router:'/myVuex'
//        }
//        ]
      }
    }
  }
</script>

<style scoped>
  .leftNavigation{
    width: 280px;
    position: fixed;
    height: 100%;
    margin-right: 20px;
    padding:30px 10px 0px 20px;
    overflow-y: scroll;
    background-color:#dddddd;
    text-align: left;
  }
  h2{
    width: 100%;
    padding: 5px 0 3px 0;
  }
  ul{
    width: 100%;
    list-style-type:none;
  }
  li{
    background-color:#75b85b;
    margin-top:5px;
    line-height: 34px;
  }
  .routerLink{
    display: block;
    width: 100%;
    height: 100%;
    color:white;
    text-decoration:none;
    text-align: center;
  }
  a:hover{
    color: #222;
  }
  .router-link-active{
    color: #222;
  }
</style>



